<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="editApkData" label-width="160px">
      <el-form-item label="版本名称" required>
        <el-input v-model="editApkData.versionName" size="small" class="add-input" placeholder="请输入版本名称"></el-input>
        <!-- <span>请输入版本名称,限制30个汉字</span> -->
      </el-form-item>

      <el-form-item label="更新说明">
        <el-input
          type="textarea"
          :rows="3"
          size="small" 
          class="add-input"
          placeholder="请输入更新说明"
          v-model="editApkData.des">
        </el-input>
      </el-form-item>

      <el-form-item label="Apk类型" required>
        <el-select v-model="editApkData.apkType" size="small" class="add-input" placeholder="请选择Apk类型">
          <el-option
            v-for="item in apkTypeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>

      <el-form-item label="下载地址" required>
        <el-input v-model="editApkData.url" size="small" class="add-input" placeholder="请输入下载地址" disabled></el-input>
        <el-upload
          style="display: inline-block;"
          action=""
          :show-file-list="false"
          :before-upload="apkUploadReg"
          :http-request="uploadApk">
          <div v-loading="apkUploadLoading">
            <el-button icon="el-icon-upload2" size="small">上传apk文件</el-button>
          </div>
        </el-upload>
      </el-form-item>

      <el-form-item label="内部版本号" required>
        <el-input v-model="editApkData.versionCode" size="small" class="add-input" placeholder="请输入内部版本号"></el-input>
        <span>内部版本号限制正整数</span>
      </el-form-item>

      <el-form-item label="升级类型" required>
        <el-select v-model="editApkData.updateType" size="small" class="add-input" placeholder="请选择升级类型">
          <el-option
            v-for="item in updateTypeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </el-form-item>
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button type="primary" size="small" @click="doSaveHandel" :disabled="isDisabled">保存</el-button>
        <el-button size="small" @click="doCancelHandel">取消</el-button>
      </el-form-item>
      
    </el-form>
  </div>
</template>

<script>
import * as qiniu from 'qiniu-js'
import Storage from '@/utils/localStorage'
 
const apk = {
  // 获取对应操作类型列表
  saveApk: '/plateform/a/apk/version/saveApk',
  // 上传文件
  getQiNiuToken: '/foundation/qiniu/auth/file'
}
  export default {
    data() {
      return {
        isDisabled: false, // 保存防止多次点击
        updateTypeOptions: [{
          value: 0,
          label: '正常'
        }, {
          value: 1,
          label: '强制升级'
        }],
        // 添加内容
        editApkData: {
          versionName: '', // 版本名称
          des: '', // 更新说明
          url: '', // 下载地址
          versionCode: '', // 内部版本号
          updateType: '', // 升级类型
          id: '',
          apkType: 0 // Apk类型
        },
        apkUploadLoading: false,
        apkFile: {},
        apkTypeOptions: [{
          value: 0,
          label: '门店端'
        }, {
          value: 1,
          label: 'TV端'
        }]
      }
    },
    mounted() {
      let ApkDate = Storage.get('ApkDate')
      if(ApkDate) {
        this.editApkData = ApkDate
      }
    },
    created() {
    },
    methods: {
      /**
       * [doSaveHandel 保存操作]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        let numReg = /^[1-9]*[1-9][0-9]*$/
        if(this.editApkData.versionName === "") {
          this.$message("请输入版本名称")
          return
        }

        if(this.editApkData.url === "") {
          this.$message("请输入下载地址")
          return
        }

        if(this.editApkData.versionCode == "") {
          this.$message("请输入内部版本号")
          return 
        }else if(!numReg.test(this.editApkData.versionCode) || this.editApkData.versionCode < 0) {
          this.$message("内部版本号格式不正确")
          return
        }

        if(this.editApkData.updateType === "") {
          this.$message("请选择升级类型")
          return
        }

        this.isDisabled = true
        this.$http.post(apk.saveApk, {...this.editApkData}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$router.push('/apk')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消操作]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/apk')
        }).catch(() => {})
      },
      /**
       * [apkUploadReg 上传apk文件检测]
       * @param  {[type]} file [description]
       * @return {[type]}      [description]
       */
      apkUploadReg (file) {
        let fileName = file.name
        let len= file.name.length
        this.apkFile = {
          'fileExtension': 'apk',
          'fileName': fileName.slice(0, len - 4)
        }
        const isApk = fileName.slice(len - 3) === 'apk'
        if(!isApk) {
          this.$message.error('文件必须是Apk格式!')
        }
        return isApk
      },
      /**
       * [uploadApk 上传apk文件]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
      uploadApk (e) {
        this.apkUploadLoading = true
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.apkUploadLoading = false
            },
            complete: (res) => {
              let urlapk = 'http://retailimg01.benlailife.com/' + res.key
              this.editApkData.url = urlapk
              this.apkUploadLoading = false
            },
            next: res => {
            }
          })
        })
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @param  {Function} callback [description]
       * @return {[type]}            [description]
       */
      getToken () {
        return this.$http.post(apk.getQiNiuToken, {...this.apkFile}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.apkUploadLoading = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      }

    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }

  }
</script>


